<script setup>
import { ref } from "vue";

const selected = {
    course: ref({
        title: "1 ",
        price: 15000,
        unit: "",
        period: 1,
        discount: 0,
    }),
    sportswearRental: ref(""),
    locker: ref(""),
};
const course = [
    { title: "1 ", price: 15000, unit: "", period: 1, discount: 0, discountPercentage: 0 },
    { title: "1 ", price: 59000, unit: "", period: 1, discount: 0, discountPercentage: 0 },
    { title: "3 ", price: 49000, unit: "", period: 3, discount: 30000, discountPercentage: 10 },
    { title: "6 ", price: 39000, unit: "", period: 6, discount: 120000, discountPercentage: 20 },
    { title: "12 ", price: 29000, unit: "", period: 12, discount: 360000, discountPercentage: 30 },
];
const sportswearRental = [
    { title: " ", price: 0, discount: 0 },
    { title: " 1", price: 10000, discount: 0 },
    { title: " 3", price: 30000, discount: 0 },
    { title: " 6", price: 50000, discount: 10000 },
    { title: " 12", price: 100000, discount: 20000 },
];
const locker = [
    { title: " ", price: 0, discount: 0 },
    { title: "  1", price: 10000, discount: 0 },
    { title: "  3", price: 30000, discount: 0 },
    { title: "  6", price: 50000, discount: 10000 },
    { title: "  12", price: 100000, discount: 20000 },
];

const totalPrice = () => {
    return Object.values(selected).reduce((accu, curr) => {
        if (!curr.value) return accu;
        return accu + curr.value.price;
    }, 0);
};
const totalDiscount = () => {
    return Object.values(selected).reduce((accu, curr) => {
        if (!curr.value) return accu;
        return accu + curr.value.discount;
    }, 0);
};
const deleteItem = (category) => {
    if (category === "course") return alert("   .");
    selected[category].value = "";
};
</script>

<template>
    <div class="main-layout place-choice-layout">
        <div class="mob-inner place-choice-layout-inner">
            <!--   -  -->
            <div class="place-choice-form">
                <div class="place-choice-form-tit">
                    <p class="f-20-700"> </p>
                    <span class="f-14-400">  </span>
                </div>
                <div class="place-choice-list-layout">
                    <div class="place-choice-list-layout-tit"></div>
                    <ul>
                        <!-- 
                              border  , active   li  
                            @include borderGradient  (place.scss 395   ). 
                        -->
                        <li
                            class="flex-flow"
                            :class="{ active: selected.course.value.title === type.title || (idx === 0 && !selected.course.value) }"
                            v-for="(type, idx) in course"
                            :key="idx"
                            @click="selected.course.value = type"
                        >
                            <input
                                type="radio"
                                :id="'ticket_1_' + idx"
                                name="ticket"
                                :value="type.title"
                                :checked="selected.course.value.title === type.title || (idx === 0 && !selected.course.value)"
                            />
                            <label :for="'ticket_1_' + idx">
                                <div class="top-row each-tit">
                                    <span class="outSide">
                                        <span></span>
                                    </span>
                                    <p class="place-ticket-list-name">{{ type.title }}</p>
                                </div>
                                <div class="bott-row">
                                    <span class="discount f-12-400" v-show="type.discount">{{ type.discount }} </span>
                                    <p class="f-16-700">
                                        <span class="percent" v-show="type.discount">{{ type.discountPercentage }}%</span> {{ type.price }}/{{ type.unit }}
                                    </p>
                                    <span class="f-12-400 place-ticket-list-pay pay"> {{ type.price * type.period }}</span>
                                </div>
                            </label>
                        </li>
                    </ul>
                </div>
            </div>
            <!--   -  /  -->
            <div class="place-choice-form">
                <div class="place-choice-form-tit">
                    <p class="f-20-700"> </p>
                    <span class="f-14-400">    </span>
                </div>
                <!--  -->
                <div class="training-opt-list place-choice-list-layout mp">
                    <div class="place-choice-list-layout-tit"></div>
                    <ul>
                        <li
                            v-for="(type, idx) in sportswearRental"
                            :key="idx"
                            :class="{ active: selected.sportswearRental.value.title === type.title || (idx === 0 && !selected.sportswearRental.value) }"
                            @click="selected.sportswearRental.value = type"
                        >
                            <input type="radio" :id="'training_1_' + idx" name="training" :value="type.title" :checked="idx === 0 && !selected.sportswearRental.value" />
                            <label :for="'training_1_' + idx">
                                <div class="left-column each-tit">
                                    <span class="outSide">
                                        <span></span>
                                    </span>
                                    <p class="training-opt-list-name">{{ type.title }}</p>
                                </div>
                                <div class="right-column">
                                    <span class="discount f-12-400" v-show="type.discount">{{ type.discount }} </span>
                                    <p class="training-opt-list-pay f-16-700 pay">{{ type.price }}</p>
                                </div>
                            </label>
                        </li>
                    </ul>
                </div>
                <!--   -->
                <div class="locker-opt-list place-choice-list-layout mp">
                    <div class="locker-opt-list-tit"> </div>
                    <ul>
                        <li
                            v-for="(type, idx) in locker"
                            :key="idx"
                            :class="{ active: selected.locker.value.title === type.title || (idx === 0 && !selected.locker.value) }"
                            @click="selected.locker.value = type"
                        >
                            <input type="radio" :id="'locker_1_' + idx" name="locker" :value="type.title" :checked="idx === 0 && !selected.locker.value" />
                            <label :for="'locker_1_' + idx">
                                <div class="left-column each-tit">
                                    <span class="outSide">
                                        <span></span>
                                    </span>
                                    <p class="training-opt-list-name">{{ type.title }}</p>
                                </div>
                                <div class="right-column">
                                    <span class="discount" v-show="type.discount">{{ type.discount }} </span>
                                    <p class="training-opt-list-pay f-16-700 pay">{{ type.price }}</p>
                                </div>
                            </label>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="bott-pay-btn">
            <div class="place-name f-14-700"></div>
            <ul class="choice-machine-list">
                <li v-for="(item, idx) in Object.entries(selected)" :key="idx" v-show="item[1].value && item[1].value.title !== ' '">
                    <div>
                        <div class="f-14-400 choice-product">{{ item[1].value.title }}</div>
                        <div class="choice-pay">
                            <span class="f-12-400 discount" v-show="item[1].value.discount">-{{ item[1].value.discount }}</span>
                            <p>{{ item[1].value.price }}</p>
                        </div>
                    </div>
                    <div class="close" @click="deleteItem(item[0])"></div>
                </li>
            </ul>
            <div class="total-pay">
                <div>
                    <div class="f-12-400">
                        <span class="lightgray">  </span><span class="lightgray">{{ totalDiscount() }} </span>
                    </div>
                    <div>
                        <span class="f-14-400"> </span><span class="f-16-700">{{ totalPrice() }}</span>
                    </div>
                </div>
                <button class="btn-44-purple f-14-700"></button>
            </div>
        </div>
    </div>
</template>
