<script setup>
import { ref, watch } from "vue";
import useAjaxRequest from "@/composables/useAjaxRequest";
import useUploadFile from "../../composables/useUploadFile";

const reviewText = ref("");
const wordCount = ref(0);
const scope = ref(5);

const allowType = ["jpg", "jpeg", "png", "gif"];
const { fileData, uploadFile, deleteFile } = useUploadFile(allowType, 8, 3);
const { postData, resData } = useAjaxRequest();

const updateWordCount = () => {
    wordCount.value = reviewText.value.length;
};

const scopeHandler = (e) => {
    scope.value = Number(e.target.dataset.idx);
};

const submitData = async () => {
    if (wordCount.value < 50) return alert("50   .");
    const formData = new FormData();
    formData.append("review_text", reviewText.value);
    formData.append("review_scope", scope.value);
    fileData.value.forEach((el) => {
        formData.append("review_image", el?.file);
    });
    const res = await postData("api", formData);
    alert(res.msg);
    if (res.code !== 200) return;
};
</script>

<template>
    <div class="ReviewWrite_container main-layout">
        <div class="ReviewWrite_container_inner mob-inner">
            <!--   -->
            <div class="product">
                <div class="left-column">
                    <p class="f-14-700"><span class="trainer-name"></span><span class="center-name">()</span></p>
                    <span class="class-name f-14-400">   30</span>
                </div>
                <div class="right-column center-img"></div>
            </div>
            <!--  ? -->
            <div class="scope-select">
                <div class="scope-select-tit f-16-700"> ?</div>
                <span class="scope-current f-12-400"></span>
                <div class="starBox">
                    <div class="star" v-for="(star, idx) in scope" :key="star" :data-idx="idx + 1" @click="scopeHandler"></div>
                    <div class="star" v-for="(star_empty, idx) in 5 - scope" :key="star_empty" :data-idx="scope + (idx + 1)" @click="scopeHandler"></div>
                </div>
            </div>
            <!--    -->
            <div class="album">
                <div class="album-tit f-16-700">  </div>
                <span class="f-12-400">
                    <span>     </span>
                    <span>  .</span>
                </span>
                <div class="album-select">
                    <div class="album-select-img" v-for="(file, idx) in fileData" :key="idx" :class="{ none: !fileData[idx] }">
                        <img v-show="fileData[idx]" :src="fileData[idx] && fileData[idx].url" />
                        <div class="close" @click="fileData.splice(idx, 1)"></div>
                    </div>
                    <button class="album-plus none" v-show="fileData.length < 3">
                        <div class="album-plus-inner">
                            <img src="@/assets/image/camera.png" alt=" " />
                            <p class="f-12-400">
                                <span>{{ fileData.length }}</span
                                >/3
                            </p>
                        </div>
                        <input type="file" id="mb-review" name="mb-review" multiple @change="uploadFile" />
                    </button>
                </div>
            </div>
            <!--    -->
            <div class="review-input">
                <div class="review-input-tit f-16-700">  </div>
                <div class="review-textarea">
                    <textarea v-model="reviewText" cols="30" rows="10" placeholder="50  ." @input="updateWordCount"></textarea>
                    <span class="word-count f-12-400">{{ wordCount }}</span>
                </div>
            </div>
            <!--   -->
            <div class="review-warning">
                <ul class="warning-list">
                    <li>
                        <span></span>
                        <p>    ,            . <router-link to="/">  </router-link></p>
                    </li>
                    <li><span></span>       .</li>
                </ul>
                <button class="btn-44-purple f-14-700" @click="submitData"></button>
            </div>
        </div>
    </div>
</template>
