import axios from "axios";
import { ref, watch } from "vue";
import useAjaxRequest from "./useAjaxRequest";

export default function useValidations(option) {
    const { postData } = useAjaxRequest();
    const form = {
        mb_email: ref(""),
        mb_password: ref(""),
        mb_password_cfm: ref(""),
        mb_nickname: ref(""),
        mb_phone: ref(""),
    };
    const isFormValid = {
        mb_email: ref(false),
        mb_password: ref(false),
        mb_password_cfm: ref(false),
        mb_nickname: ref(false),
        mb_nickname_dup: ref(false),
        mb_phone: ref(false),
    };
    console.log(form.mb_email.value);
    const errorText = {
        mb_email: "  .",
        mb_nickname: "   .",
        mb_nickname_dup: "  .",
        mb_phone: "   .",
        mb_password: " , ,   8  .",
        mb_password_cfm: "   .",
    };
    const isPwdVisible = ref(false);
    const isPwdConfirmedVisible = ref(false);
    const exp = {
        mb_email: /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i,
        mb_nickname: /^(?=.*[a-z0-9-])[a-z0-9-]{2,10}$/,
        // 2  10 ,      
        mb_phone: /^(01[016789]{1})[0-9]{3,4}[0-9]{4}$/,
        mb_password: /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,30}$/,
        mb_password_cfm: /^[-]{2,4}|[a-zA-Z]{2,10}\s[a-zA-Z]{2,10}$/,
    };
    watch(form.mb_email, (newValue) => {
        isFormValid.mb_email.value = exp.mb_email.test(newValue);
    });
    watch(form.mb_nickname, (newValue) => {
        isFormValid.mb_nickname.value = exp.mb_nickname.test(newValue);
        isFormValid.mb_nickname_dup.value = false;
    });
    watch(form.mb_phone, (newValue) => {
        isFormValid.mb_phone.value = exp.mb_phone.test(newValue);
    });
    watch(form.mb_password, (newValue) => {
        isFormValid.mb_password.value = exp.mb_password.test(newValue);
        isFormValid.mb_password_cfm.value = false;
    });
    watch([form.mb_password_cfm, form.mb_password], () => {
        isFormValid.mb_password_cfm.value = form.mb_password.value === form.mb_password_cfm.value;
    });
    const nextCondition = () => {
        if (!option)
            return Object.values(isFormValid).every((el) => {
                return el.value;
            });
        else
            return option.every((el) => {
                return isFormValid[el].value;
            });
    };
    const showPwd = () => {
        isPwdVisible.value = !isPwdVisible.value;
    };

    const showPwdConfirmed = () => {
        isPwdConfirmedVisible.value = !isPwdConfirmedVisible.value;
    };
    const dupCheckNickname = async () => {
        const res = await postData("user/dup_check/", { type: "nickname", value: form.mb_nickname.value });
        if (res.data) isFormValid.mb_nickname_dup.value = true;
    };

    return { form, isFormValid, errorText, isPwdVisible, isPwdConfirmedVisible, nextCondition, showPwd, showPwdConfirmed, dupCheckNickname };
}
