<script setup>
import { ref } from "vue";

const selected = {
    course: ref({ title: "1:1 PT 1 ", price: 29900, unit: "", discount: 0, discountPercentage: 0 }),
    sportswearRental: ref(""),
    locker: ref(""),
};
const course = {
    personalPT: [
        { title: "1:1 PT 1 ", price: 29900, unit: "", ptCount: 1, discount: 0, discountPercentage: 0 },
        { title: "1:1 PT 10", price: 49900, unit: "", ptCount: 10, discount: 0, discountPercentage: 0 },
    ],
    groupPT: [{ title: "2:1 PT 10", price: 39900, unit: "", ptCount: 10, discount: 10000, discountPercentage: 20 }],
    bodyProfile: [{ title: "   30", price: 47900, unit: "", ptCount: 30, discount: 0, discountPercentage: 0 }],
};
const sportswearRental = [
    { title: " ", price: 0, discount: 0 },
    { title: " 1", price: 10000, discount: 0 },
    { title: " 3", price: 30000, discount: 0 },
    { title: " 6", price: 50000, discount: 10000 },
    { title: " 12", price: 100000, discount: 20000 },
];
const locker = [
    { title: " ", price: 0, discount: 0 },
    { title: "  1", price: 10000, discount: 0 },
    { title: "  3", price: 30000, discount: 0 },
    { title: "  6", price: 50000, discount: 10000 },
    { title: "  12", price: 100000, discount: 20000 },
];

const totalPrice = () => {
    return Object.values(selected).reduce((accu, curr) => {
        if (!curr.value) return accu;
        return accu + curr.value.price;
    }, 0);
};
const totalDiscount = () => {
    return Object.values(selected).reduce((accu, curr) => {
        if (!curr.value) return accu;
        return accu + curr.value.discount;
    }, 0);
};
const deleteItem = (category) => {
    if (category === "course") return alert("   .");
    selected[category].value = "";
};
</script>

