<script setup>
import { ref } from "vue";
import { useRoute } from "vue-router";
import Alert from "./components/Alert";
import Search from "./components/Search";

const route = useRoute();

const AlertOpen = ref(false);
const SearchOpen = ref(false);
const headerActive = ref(["JoinStep1", "JoinStep2", "JoinStep3", "JoinComplete", "Authority"]);

const headerBottBorder = ref(
    [
        "Terms", 
        "InquiryComplete", 
        "Coupon", 
        "OrderInquiry", "OrderInquiryView", "CancelInquiry",
        "MyPage", "Profile_Type1", "Profile_Type2", 
        "Filter",
        "ReviewSelect", "ReviewWrite",
        "PlaceMachine_Choice", "PlacePay", "PlacePay_Complete", "PlaceTrainer", "PlaceMachine", "PlaceReview",
        "TrainerMachine_Choice", "TrainerPay_Complete", "TrainerTicket_Complete", "TrainerReview"
    ]
);

//     
const headerThrough = ref(
    [
        "Home", "NotClassHome", "ClassHome", "ProgressHome", "CompleteHome"
    ]
)

const AlertOpenFnc = () => {
    AlertOpen.value = !AlertOpen.value;
    SearchOpen.value = false;
}; 

const SearchOpenFnc = () => {
    SearchOpen.value = !SearchOpen.value;
    AlertOpen.value = false;
};

const headerType = () => {
    switch (route.fullPath) {
        case "/":
        case "/Class":
        case "/class/":
        case "/progress/class":
        case "/not/class/trainer":
        case "/complete/class": {
            return {
                logo: true,
                alarm: true,
                search: true,
                mypage: true,
            };
        }
        case "/join/step1": {
            return {
                back: true,
                overallProgress: 3,
                currentProgress: 1,
            };
        }
        case "/join/step2": {
            return {
                back: true,
                overallProgress: 3,
                currentProgress: 2,
            };
        }
        case "/join/step3": {
            return {
                back: true,
                overallProgress: 3,
                currentProgress: 3,
            };
        }
        case "/authority":
        case "/place/pay/complete":
        case "/place/ticket/complete":
        case "/trainer/pay/complete":
        case "/Inquiry/complete":
        case "/Review/Select":
        case "/Review/Write": {
            return {
                close: true,
            };
        }
        case "/filter": {
            return {
                back: true,
                close: true,
            };
        }
        case "/place/detail": {
            return {
                back: true,
                search: true,
                mypage: true,
            };
        }
        case "/Terms":
        case "/terms":
        case "/Inquiry":
        case "/faq":
        case "/event-news":
        case "/coupon":
        case "/orderinquiry":
        case "/orderinquiryView":
        case "/cancelInquiry":
        case "/mypage":
        case "/MyPage": {
            return {
                back: true,
                home: true,
            };
        }
        case "/review/detail":
        case "/my-review": {
            return {
                back: true,
                write: true,
                search: true,
                mypage: true,
            };
        }
        case "/discover": {
            return {
                search: true,
                mypage: true,
            };
        }
        case "/review": {
            return {
                back: false,
                write: true,
                search: true,
                mypage: true,
            };
        }
        case "/wish": {
            return {
                back: false,
                search: true,
                mypage: true,
            };
        }
        default: {
            return {
                back: true,
            };
        }
    }
};

const headerTitle = () => {
    switch (route.fullPath) {
        case "/find/id/step1":
        case "/find/id/step2": {
            return " ";
        }
        case "/find/pw/step1":
        case "/find/pw/step2": {
            return " ";
        }
        case "/find/pw/email/step1": {
            return "  ";
        }
        case "/pw/reset":
        case "/PW_Reset": {
            return " ";
        }
        case "/join/step1": {
            return "  ";
        }
        case "/join/step2": {
            return "";
        }
        case "/join/step3": {
            return " ";
        }
        case "/filter": {
            return "";
        }
        case "/place/machine": {
            return "";
        }
        case "/place/machine-choice": {
            return " ";
        }
        case "/place/pay": {
            return "";
        }
        case "/place/pay/complete":
        case "/place/ticket/complete":
        case "/trainer/pay/complete": {
            return " ";
        }
        case "/place/review":
        case "/trainer/review": {
            return " ";
        }
        case "/Place/Trainer": {
            return "1:1 ";
        }
        case "/Terms":
        case "/terms": {
            return "  ";
        }
        case "/faq": {
            return "  ";
        }
        case "/coupon": {
            return "";
        }
        case "/orderinquiry": {
            return "";
        }
        case "/orderinquiryView":
        case "/OrderInquiryView": {
            return " ";
        }
        case "/cancelInquiry": {
            return " ";
        }
        case "/Account/Delete": {
            return " ";
        }
        case "/MyPage": {
            return "";
        }
        case "/Review/Select":
        case "/Review/Write": {
            return " ";
        }
        case "/interest/region": {
            return " ";
        }
        case "/profile_email_login":
        case "/profile_sns_login":
        case "/Profile_sns_login": {
            return "";
        }
        case "/Inquiry":
        case "/Inquiry/complete": {
            return " ";
        }
        case "/Change/Nickname":
        case "/change/nickname": {
            return " ";
        }
        case "/Change/Password":
        case "/change/password":
        case "/Change/Password/Confirm":
        case "/change/password/confirm": {
            return " ";
        }
        default: {
            return false;
        }
    }
};

const pageName = () => {
    switch (route.fullPath) {
        case "/discover": {
            return "";
        }
        case "/review": {
            return "";
        }
        case "/wish": {
            return "";
        }
    }
};
</script>

<template>
    <header
        class="at-header"
        :class="[
            headerActive.includes($route.name) ? 'active' : '', 
            headerBottBorder.includes($route.name) ? 'border' : '',
            $route.name === 'DiscoverList' ? 'discover' : '',
            headerThrough.includes($route.name) ? 'through' : '',
        ]"
    >
        <span 
            class="header-current"
            :style="{
            backgroundImage: 
                `linear-gradient(90deg, #9161E1, #44A5E1, #00FD65 ${(headerType()['currentProgress'] / headerType()['overallProgress']) * 100}%, 
                #fff ${1 - (headerType()['currentProgress'] / headerType()['overallProgress']) * 100
                }%)`,
            }">
        </span>
        <div class="at-header-inner">
            <div class="left">
                <div v-if="headerType()['back']" @click="$router.go('-1')" class="back">
                    <img src="@/assets/image/back.png" alt="">
                </div>
                <div v-if="headerType()['logo']" class="logo">
                    <img class="logo-img" v-if="headerThrough.includes($route.name)" src="@/assets/image/logo.png" alt=" " />
                    <img class="logo-img" v-else src="@/assets/image/logo_b.png" alt=" ">
                </div>
                <h1 v-if="pageName()" class="f-20-700">{{ pageName() }}</h1>
            </div>
            <div class="center">
                <h1 v-if="headerTitle()" class="f-16-400">{{ headerTitle() }}</h1>
            </div>
            <div class="right">
                <ul class="menu-ul">
                    <li class="alarm" v-if="headerType()['alarm']" @click="AlertOpenFnc">
                        <!--             . -->
                        <img src="@/assets/image/alarm.png" alt="" />

                        <div class="notConfirm-alarm"><span>1</span></div>
                    </li>
                    <li v-if="headerType()['write']">
                        <img src="@/assets/image/write.png" alt="" />
                    </li>
                    <li class="search" v-if="headerType()['search']" @click="SearchOpenFnc">
                        <span></span>
                    </li>
                    <li 
                        v-if="headerType()['mypage']"
                        @click="
                            AlertOpen = false;
                            SearchOpen = false;
                        "
                    >
                        <router-link to="/MyPage"><img src="@/assets/image/person.png" alt="" /></router-link>
                    </li>
                    <li v-if="headerType()['home']">
                        <router-link to="/"><img src="@/assets/image/home.png" alt="" /></router-link>
                    </li>
                    <li v-if="headerType()['setting']">
                        <img src="@/assets/image/setting.png" alt="" />
                    </li>
                    <li class="close" v-if="headerType()['close']">
                        <div></div>
                    </li>
                    <li v-if="headerType()['overallProgress']" class="progress">{{ headerType()["currentProgress"] }}/{{ headerType()["overallProgress"] }}</li>
                </ul>
            </div>

            <div class="discover-place f-16-400" v-if="$route.name === 'DiscoverList'">  </div>
        </div>

        <Alert :class="{ active: AlertOpen }" />
        <Search :class="{ active: SearchOpen }" />
    </header>
</template>
