<script setup>
import { ref } from "vue";
import ReviewReport from '@/components/modal/review/ReviewReport';
import ReviewReportConfirm from '@/components/modal/review/ReviewReportConfirm';

// 
const reviewReport = ref(false);
const reportReason = () => {
    reviewReport.value = !reviewReport.value;
};

//  
const reportReview = ref(false);
const reportConfirm = () => {
    reportReview.value = !reportReview.value;
};
</script>

<template>
    <div class="ReviewDetail_container main-layout review_layout">
        <div class="ReviewDetail_container_inner mob-inner review_layout_inner">
            <ul class="review-detail-ul">
                <li class="review-detail-li" v-for="reviewItem in 4" :key="reviewItem">
                    <!--   -->
                    <div class="review-img-box"></div>
                    <!--    -->
                    <div class="review-writer">
                        <div class="left-row">
                            <div class="user-profile"></div>
                            <div class="writer-info">
                                <p class="f-14-700"><span class="f-12-400">3 </span></p>
                                <img src="@/assets/image/star_box.png" alt="">
                            </div>
                        </div>
                        <div class="right-row">
                            <div class="like-box"><img src="@/assets/image/like.png" alt=""><span class="like-count f-14-700">6</span></div>
                            <div class="menu-dot">
                                <span></span>
                                <ul class="review-sorting-list">
                                    <li><router-link to="/"></router-link></li>
                                    <li><button @click="reportReason"></button></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <!--   -->
                    <div class="review-comment f-14-400">   10kg  !       !</div>
                    <!--    -->
                    <div class="trainer-info">
                        <div class="img-box"></div>
                        <div class="txt-box">
                            <div class="info">
                                <div class="f-14-700 trainer-center-name">
                                    <p> </p>
                                    <span></span>
                                </div>
                                <div class="scope">
                                    <div class="starBox">
                                        <div class="star"></div>
                                        <p class="f-12-400">4.3<span>(12)</span></p>
                                    </div>
                                </div>
                            </div>
                            <div class="heart"><img src="@/assets/image/heart-off.png" alt=""></div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>

        <!--    -->
        <ReviewReport :class="{ active: reviewReport }" :reportReason="reportReason" :reportConfirm="reportConfirm" />
        <!--    -->
        <ReviewReportConfirm :class="{ active: reportReview }" :reportConfirm="reportConfirm" />
    </div>
</template>